/* Start CSS Global */
.hr-border {
  border-bottom: 1px solid rgb(182, 180, 180) !important;
}
@media (max-width: 600px) {
  .text-center-sm {
    text-align: center !important;
  }
}
@media only screen and (min-width: 601px) {
  #login-section {
    height: 100vh;
    background-image: linear-gradient(to right, rgb(26, 234, 182) 35%, white 0);
  }
}
@media all and (device-width: 768px) and (device-height: 1024px) and (orientation:portrait) {
  #login-section {
    height: 100vh;
    background-image: linear-gradient(to right, rgb(26, 234, 182) 20%, white 0);
  }  
}
@media only screen and (max-width: 600px) {
  #login-section {
    height: 100vh;
    background-image: linear-gradient(to right, rgb(26, 234, 182) 10%, white 0);
  }
}
/* End CSS Global */

#login-inner-section {
  background-image: linear-gradient(to right, transparent 35%, white 0);
  box-shadow: 0px 0px 20px rgba(128, 128, 128, 0.8);
}
.login-submit-button,
.login-register-button {
  background-color: rgb(26, 234, 182) !important;
  width: 60% !important;
}
